"use client"

import React, { useEffect, useRef, useState } from "react"
import { useVisibleCount } from "@/hooks/useVisibleCount"

import { Button } from "@/components/shadcn/ui/button"
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/shadcn/ui/card"
import {
  Pagination,
  PaginationContent,
  PaginationEllipsis,
  PaginationItem,
  PaginationLink,
  PaginationNext,
  PaginationPrevious,
} from "@/components/shadcn/ui/pagination"
import { Input } from "@/components/shadcn/ui/input"
import { Label } from "@/components/shadcn/ui/label"
import {
  Tabs,
  TabsContent,
  TabsList,
  TabsTrigger,
} from "@/components/shadcn/ui/tabs"
import { Separator } from "@/components/shadcn/ui/separator"
import { FilterIcon, Star, StarOff, Globe, Lock } from "lucide-react"
import { Avatar, AvatarFallback } from "@/components/shadcn/ui/avatar"
import { Badge } from "@/components/shadcn/ui/badge"

type ItemProps = {
  id: number
  title: string
  isPublic: boolean
  language: string
  owner: string
  updatedAt: string
  isStarred: boolean
  category: string
}

const dataList: ItemProps[] = [
  {
    id: 1,
    title: "Sinh vật",
    category: "Khoa học",
    isPublic: true,
    language: "Tiếng Việt",
    owner: "Nguyễn Văn A",
    updatedAt: "2025-07-01T10:00:00Z",
    isStarred: true,
  },
  {
    id: 2,
    title: "Động vật",
    category: "Sinh học",
    isPublic: false,
    language: "Tiếng Anh",
    owner: "Trần Thị B",
    updatedAt: "2025-07-05T14:30:00Z",
    isStarred: false,
  },
  {
    id: 3,
    title: "Thực vật",
    category: "Sinh học",
    isPublic: true,
    language: "Tiếng Việt",
    owner: "Nguyễn Văn C",
    updatedAt: "2025-07-08T09:00:00Z",
    isStarred: false,
  },
  // ✅ thêm nhiều item để kiểm tra scroll
  ...Array.from({ length: 1000 }, (_, i) => ({
    id: i + 4,
    title: `Mục ${i + 4}`,
    category: "Thể loại",
    isPublic: i % 2 === 0,
    language: "Tiếng Việt",
    owner: `Người ${i + 4}`,
    updatedAt: new Date().toISOString(),
    isStarred: i % 3 === 0,
  })),
]

const LibraryCard = ({ item }: { item: ItemProps }) => {
  return (
    <div
      onClick={() => console.log("Clicked", item.id)}
      className="rounded-xl bg-gray-100 dark:bg-gray-800 p-4 shadow hover:shadow-md hover:bg-gray-200 dark:hover:bg-gray-700 transition-all cursor-pointer"
    >
      <div className="flex justify-between items-start mb-3">
        <h3 className="text-base font-semibold">{item.title}</h3>
        {item.isStarred ? (
          <Star className="w-4 h-4 text-yellow-400" />
        ) : (
          <StarOff className="w-4 h-4 text-gray-400" />
        )}
      </div>
      <div className="grid grid-cols-2 gap-2 text-sm text-muted-foreground mb-3">
        <div className="flex items-center space-x-1">
          {item.isPublic ? (
            <>
              <Globe className="w-3 h-3" />
              <span>Công khai</span>
            </>
          ) : (
            <>
              <Lock className="w-3 h-3" />
              <span>Riêng tư</span>
            </>
          )}
        </div>
        <div>
          <span className="font-medium">Ngôn ngữ:</span> {item.language}
        </div>
        <div className="col-span-2">
          <span className="font-medium">Cập nhật:</span>{" "}
          {new Date(item.updatedAt).toLocaleDateString("vi-VN")}
        </div>
      </div>
      <div className="flex items-center space-x-2">
        <Avatar className="h-6 w-6">
          <AvatarFallback>
            {item.owner
              .split(" ")
              .map((w) => w[0])
              .join("")
              .slice(0, 2)}
          </AvatarFallback>
        </Avatar>
        <span className="text-sm">{item.owner}</span>
        <Badge variant="outline" className="ml-auto text-xs">
          #{item.category}
        </Badge>
      </div>
    </div>
  )
}

export default function MainPage() {
  const [visibleItems, setVisibleItems] = useState<ItemProps[]>([])
  const itemsPerLoad = 6
  const loadMoreRef = useRef<HTMLDivElement | null>(null)

  // Load initial data
  useEffect(() => {
    setVisibleItems(dataList.slice(0, itemsPerLoad))
  }, [])

  // Infinite Scroll Observer
  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        const isVisible = entries[0].isIntersecting
        if (isVisible) {
          const currentLength = visibleItems.length
          const next = dataList.slice(
            currentLength,
            currentLength + itemsPerLoad
          )
          if (next.length > 0) {
            setVisibleItems((prev) => [...prev, ...next])
          }
        }
      },
      {
        rootMargin: "0px",
        threshold: 1.0,
      }
    )

    if (loadMoreRef.current) {
      observer.observe(loadMoreRef.current)
    }

    return () => {
      if (loadMoreRef.current) {
        observer.unobserve(loadMoreRef.current)
      }
    }
  }, [visibleItems])

  return (
    <Tabs defaultValue="1" className="w-full">
      <TabsList className="grid w-[400px] grid-cols-3">
        <TabsTrigger value="1">Thư viện của tôi</TabsTrigger>
        <TabsTrigger value="2">Đã lưu</TabsTrigger>
        <TabsTrigger value="3">Khám phá</TabsTrigger>
      </TabsList>

      <TabsContent value="1">
        <Card>
          <CardHeader>
            <CardTitle>Thư viện của tôi</CardTitle>
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                <Input placeholder="Tìm kiếm..." className="w-[200px]" />
                <Button variant="outline" size="icon">
                  <FilterIcon className="h-4 w-4" />
                </Button>
              </div>
              <Button>Thêm mới</Button>
            </div>
          </CardHeader>

          <CardContent>
            <div className="grid grid-cols-[repeat(auto-fill,_minmax(250px,_1fr))] gap-4">
              {visibleItems.map((item) => (
                <LibraryCard key={item.id} item={item} />
              ))}
            </div>

            {visibleItems.length < dataList.length && (
              <div
                ref={loadMoreRef}
                className="h-10 mt-4 flex justify-center items-center"
              >
                <span className="text-sm text-muted-foreground">
                  Đang tải thêm...
                </span>
              </div>
            )}
          </CardContent>
        </Card>
      </TabsContent>

      {/* Các Tabs khác giữ nguyên hoặc update tương tự nếu muốn */}
      <TabsContent value="2">
        <Card>
          <CardHeader>
            <CardTitle>Đã lưu</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-muted-foreground">Chưa có nội dung.</p>
          </CardContent>
        </Card>
      </TabsContent>

      <TabsContent value="3">
        <Card>
          <CardHeader>
            <CardTitle>Khám phá</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-muted-foreground">Khám phá nội dung mới.</p>
          </CardContent>
        </Card>
      </TabsContent>
    </Tabs>
  )
}
